//----------  typography
//------------------------------------------------------------------------------

.rte h1, .rte h2, .rte h3, .rte h4, .rte h5, .rte h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
	font-family: $font-default;
	font-weight: normal;
	line-height: 1.35em;
}

.rte h1, .h1 {
	font-size: 48px;
}

.rte h2, .h2 {
	font-size: 36px;
}

.rte h3, .h3 {
	font-size: 30px;
}

.rte h4, .h4 {
	font-size: 24px;
}

.rte h5, .h5 {
	font-size: 20px;
}

.rte h6, .h6 {
	font-size: 16px;
}

.rte {
	p {
		margin-bottom: 15px;
		a {
			@include hover {
				text-decoration: underline;
			}
		}
	}
	blockquote {
		border-top: 1px solid $black;
		border-bottom: 1px solid $black;
		padding: 30px 0;
	}
	ul {
		padding-left: 20px;
	}
	ol {
		padding-left: 20px;
	}
	ul li {
		list-style-type: disc;
	}
	ol li {
		list-style-type: decimal-leading-zero;
	}
}

img {
	max-width: 100%;
}